import axios from 'axios'

const url = "https://aquiestoy-joe.herokuapp.com/"

export const createPub = (pubData) => {
   return axios.post(url + "anuncios",  pubData,{
       headers:{
           'Authorization':localStorage.getItem('token')
       }
   })
       .then(event=>{
           return event
       })
       .catch(error=>{
           return error
       })
}

export const getPub = () =>{
    return axios.get(url + "anuncios",{
        headers:{
            'Authorization':localStorage.getItem('token')
        }
    })
    .then(event=>{
        return event
    })
    .catch(error=>{
        return error
    })
}