import React, {Component} from 'react'
import AnunciosDisplay from './AnunciosDisplay';
import {createPub,getPub} from '../../services/anunciosService'
import toastr from 'toastr'
//import { Link} from 'react-router-dom'

//import Card from './Anuncios';




class AnunciosContainer extends Component{

    state = {
        pubData:{},
        pubs:[],
        loading:false
     }

     componentWillMount(){
        const user = JSON.parse(localStorage.getItem('user'))
        if(!user) return this.props.history.push('/login')
        this.setState({user})
        //this.getPubs()
    }

        onChange = (e) => {
            const field = e.target.name
            const value = e.target.value
            const {pubData} = this.state
            pubData[field] = value
            console.log(pubData)
            this.setState({pubData})
        } 
     
        onChangeFile = (e) => {
            const field = "imageURL"
            const {pubData} = this.state
            pubData[field] = e.target.files[0]
            this.setState({pubData})
        }
             

        getPubs = ()=>{
            getPub()
            .then(pubs=>{
                this.setState({pubs})
                console.log(pubs)
            })
            .catch(e=>{
                //return toastr.error('Algo salio mal')}
                console.log(e)
                })
        }



        newPub = (e) =>{
            this.setState({loading:true})
            //e.preventDefault()
            const {pubData, pubs} = this.state
            createPub(pubData)
            .then(r=>{
                this.setState({pubs})
                console.log(r.data)
                toastr.success("Publicado!")
                this.props.history.push('/anuncios')
                
            })
            .catch(e=>{
                this.setState({loading:false})
                return toastr.error("Algo salio mal.")
            })
        }


    render(){
        //const {pubs} = this.state
        return(
            <div>
              <AnunciosDisplay 
              onChange={this.onChange}
              onSubmit={this.newPub}
              loading={this.loading}
              onChangeFile={this.onChangeFile}
              />
               
              
            </div>
        )
    }
}




export default AnunciosContainer

